.options-list {
  display: flex;
  width: 100%;
  max-width: 1160px;
  margin: 0 auto;
  flex-wrap: wrap;
}

.options-list__item {
  margin-right: 40px;
  padding: 7px;
  width: 31.035%;
  background-color: #ffffff;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  z-index: 2;
  margin-bottom: 40px;

  &:nth-of-type(3n) {
    margin-right: 0;
  }

  &:hover {
    background: linear-gradient(135deg, #d10a42 0%, #00359f 100%);
  }
}

.options-list__wrapp {
  width: 100%;
  background-color: #ffffff;
  padding-top: 55px;
  padding-bottom: 60px;
  text-align: center;
  min-height: 300px;
  box-sizing: border-box;

  & h3 {
    margin: 0;
    padding: 0;
    font-size: 26px;
    line-height: 29px;
    margin-bottom: 10px;
  }

  & p {
    margin: 0;
    padding: 0;

    &:first-of-type {
      margin-bottom: 66px;
    }

    &:last-of-type {
      width: 206px;
      margin: 0 auto;
      position: relative;
      font-size: 60px;
      line-height: 90px;
      font-weight: bold;
      color: #103aae;
      z-index: 1;
      margin-bottom: 34px;

      & span {
        position: absolute;
        font-size: 100px;
        line-height: 150px;
        color: #e8e8e8;
        opacity: 0.48;
        left: 0;
        top: -55px;
        z-index: -1;
      }

      & svg {
        width: 30px;
        height: 42px;
      }
    }
  }
}

.options-list__btn {
  width: 60%;
  margin: 0 auto;
  display: block;
  text-decoration: none;
  color: #102654;
  border: 2px solid #102654;
  box-sizing: border-box;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 23px;
  padding-bottom: 24px;
  font-size: 12px;
  font-weight: bold;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;

  &:hover {
    background: linear-gradient(90deg, #d10a42 0%, #00359f 100%);
    color: #ffffff;
    border: transparent;
    padding-bottom: 26px;
    padding-top: 25px;
  }
}

@media (max-width: $table-width) {
  .options-list {
    width: 65%;
    flex-direction: column;
    justify-content: space-between;
  }

  .options-list__item {
    width: 100%;
    margin-bottom: 40px;
  }

  .options-list__wrapp {
    & h3 {
      line-height: 29px;
    }
  }

  .options-list__btn {
    width: 49%;
    padding-top: 24px;
  }
}

@media (max-width: $mobile-width) {
  .options-list {
    width: 100%;
    max-width: 550px;
  }

  .options-list__item {
    margin-bottom: 35px;
  }

  .options-list__wrapp {
    padding-top: 43px;
    padding-bottom: 46px;

    & h3 {
      font-size: 22px;
      line-height: 24px;
    }

    & p {
      &:first-of-type {
        margin-bottom: 54px;
      }

      &:last-of-type {
        font-size: 48px;
        line-height: 72px;
        margin-bottom: 30px;

        & span {
          font-size: 80px;
          line-height: 120px;
          left: 22px;
          top: -44px;
        }

        & svg {
          width: 24px;
          height: 33px;
        }
      }
    }
  }

  .options-list__btn {
    width: 75%;
  }
}
