.trainers {
  position: relative;
  padding-top: 119px;
  padding-bottom: 119px;
  width: 100%;
  margin: 0 auto;
  box-sizing: border-box;
  padding-left: 103px;
  padding-right: 103px;

  & h2 {
    font-family: $Bebas-Neue;
    font-size: 49px;
    line-height: 49px;
    letter-spacing: 0.03em;
    margin: 0;
    margin-bottom: 52px;
  }
}

.trainers__wrapp {
  max-width: 1160px;
  position: relative;
  margin: 0 auto;
}

.trainers__list {
  display: flex;
  width: 208%;
  margin: 0;
  padding: 0;
  list-style: none;
  transition: margin-left 650ms;
}

.trainers__item {
  display: flex;
  width: 260px;
  height: auto;
  margin-right: 40px;
  position: relative;
  cursor: pointer;
  flex-shrink: 0;

  & > p {
    position: absolute;
    text-align: center;
    width: 100%;
    bottom: 9%;
    left: 50%;
    transform: translateX(-50%);
    font-weight: bold;
    font-size: 18px;
    line-height: 27px;
    color: #ffffff;
    margin: 0;

    & span {
      font-size: 16px;
      line-height: 19px;
      font-weight: normal;
      display: none;
      color: #b6bdcc;
    }
  }

  &:hover {
    & p {
      padding-top: 52px;
      padding-left: 57px;
      padding-right: 57px;
      box-sizing: border-box;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(16, 38, 84, 0.89);
      text-align: center;

      & span {
        display: flex;
        justify-content: center;
      }
    }

    & .merit-list {
      display: block;
    }
  }
}

.trainers__slider {
  width: 100%;
  max-width: 1160px;
  margin: 0 auto;
  overflow: hidden;
}

.trainers__arrow {
  position: absolute;
  border: none;
  background-color: transparent;
  top: 57.5%;
  cursor: pointer;
  width: 30px;
  outline: none;

  &:hover svg path {
    stroke: #000000;
  }
}

.trainers__arrow--prev {
  left: -63px;
}

.trainers__arrow--next {
  right: -63px;
}

@media (max-width: $table-width) {
  .trainers {
    text-align: center;
    padding-top: 99px;
    padding-bottom: 100px;
    padding-left: 101px;
    padding-right: 101px;

    & h2 {
      font-size: 42px;
      line-height: 42px;
      margin-bottom: 42px;
    }
  }

  .trainers__wrapp {
    max-width: 566px;
  }

  .trainers__list {
    width: 9999999px;
  }

  .trainers__slider {
    width: 566px;
    margin: 0 auto;
    overflow: hidden;
  }

  .trainers__item {
    width: 268px;
    margin-right: 30px;
  }

  .trainers__arrow {
    top: 212px;
  }
}

@media (max-width: $mobile-width) {
  .trainers {
    padding-top: 69px;
    padding-bottom: 64px;
    padding-left: 47px;
    padding-right: 47px;

    & h2 {
      font-size: 32px;
      line-height: 32px;
    }
  }

  .trainers__wrapp {
    max-width: 320px;
  }

  .trainers__slider {
    width: 226px;
    margin: 0 auto;
    overflow: hidden;
  }

  .trainers__arrow {
    width: 23px;
    top: 201px;
  }

  .trainers__arrow--prev {
    left: -39px;
  }

  .trainers__arrow--next {
    right: -39px;
  }

  .trainers__item {
    width: 226px;
  }
}
